<template>
    <div class="container mt-5">
        <form @submit.prevent="signupForm">
            <div class="input-Box">
                <label for="inputname" class="form-label"></label>
                <input type="text" id="inputname" class="form-control" placeholder="" v-model="state.form.name">
            </div>
            <div class="input-Box">
                <label for="inputId" class="form-label">ID</label>
                <input type="text" id="inputId" class="form-control" placeholder="ID " v-model="state.form.id" maxlength=16 @blur="idValid">
                <div v-if="!state.idValidFlag" class="form-text">   .    8-16  .</div>
                <div v-if="state.idCommonFlag == true" class="form-text">   .</div>
                <div v-if="state.idCommonFlag == false" class="form-text">  .</div>
            </div>
            <div class="input-Box" id='no-flex'>
                <label for="inputPassword" class="form-label">Password</label>
                <input type="password" id="inputPassword" class="form-control" placeholder=" " aria-describedby="passwordHelpBlock" v-model="state.form.password1" @blur="passwordValid">
                <div id="passwordHelpBlock" class="form-text mb-3">
                      , ,   8-16  .
                </div>
                <div v-if="!state.passwordValidFlag" class="form-text">  .</div>
                <input type="password" id="inputPassword2" class="form-control" placeholder=" " v-model="state.form.password2" @blur="passwordCheckValid">
                <div v-if="!state.passwordCheckFlag" class="form-text">  .</div>
            </div>
            <hr class="mt-5 mb-4">
             <div class="input-Box">
                <label for="inputEmail" class="form-label">Email</label>
                <input type="email" class="form-control" id="inputEmail" v-model="state.form.email" @blur="emailValid">
                <div v-if="!state.emailValidFlag" class="form-text">  .</div>
                <div v-if="state.emailCommonFlag == true" class="form-text">   .</div>
                <div v-if="state.emailCommonFlag == false" class="form-text">  .</div>
            </div>
            <div class="input-Box">
                <label for="selectcategory" class="custom-label"> </label>
                <select v-model="state.form.interest" id="selectcategory" class="form-select" aria-label="Default select example">
                    <!-- <option selected></option> -->
                    <option value="what" selected> </option>
                    <option value="C">C</option>
                    <option value="C++">C++</option>
                    <option value="JAVA">JAVA</option>
                    <option value="Python">Python</option>
                    </select>
            </div>
            <div class="input-Box">

              <router-link to="/home"><button> </button></router-link>
              <input type="submit" value="">  

            </div>
        </form>  
    </div>
</template>

<script>
import { reactive } from 'vue'
import http from "@/util/http-common.js";
import { useStore } from 'vuex'
import axios from 'axios'

export default {
    name: 'signupView',

     setup() {
        const store = useStore()
        const state = reactive({
            form:{
                name: null,
                id : null, 
                password1: null,
                password2: null,
                email: null,
                interest: "what",
            },
            idValidFlag: true,
            passwordValidFlag: true,
            passwordCheckFlag: true,
            emailValidFlag : true,
            idCommonFlag : null,
            emailCommonFlag :null,

     })
        const signupForm = function(){
            if (state.form.name == null || state.form.id == null || state.form.password1 == null || state.form.password2 == null || state.form.email == null || state.form.interest == null) {
                alert(' ')
                return
            }
            if (!state.idValidFlag || !state.passwordValidFlag || !state.passwordCheckFlag || !state.emailValidFlag || state.idCommonFlag == true) {
                alert('    .')
                return
            }
            store.dispatch('signup',{
                "email":state.form.email,
                "id":state.form.id,
                "interest":state.form.interest,
                "name":state.form.name, 
                "password":state.form.password1, 
                "photo": "../../assets/profile4.png"})
            .then(function (){
                alert('Goose   !')
                store.dispatch('login',{
                    'id': state.form.id,
                    'password': state.form.password1
                })
            })
            .catch(function(err){
                alert(err)
            })}

        const idValid = async function() {
            if (/^[a-z]+[a-z0-9]{5,19}$/.test(state.form.id)){
                if (state.form.id.includes(' ')){
                    state.idValidFlag = false
                }
                else {
                    state.idValidFlag = true
                    state.checkValidFlag = false
                    try{
                        let data = await http({
                            method: 'get',
                            url: `/users/{userId}?userId=${state.form.id}`
                        })
                        state.idCommonFlag = true
                    } catch(err) {
                        state.idCommonFlag = false
                    }
                }
                
            } else {
                state.idValidFlag = false
                state.idCommonFlag = null
            }
        }

        const passwordValid = function() {
            if (/^(?=.*[a-zA-z])(?=.*[0-9])(?=.*[$`~!@$!%*#^?&\\(\\)\-_=+]).{8,16}$/.test(state.form.password1)) {
                if (state.form.password1.includes(' ')){
                    state.passwordValidFlag = false
                }
                else {
                    state.passwordValidFlag = true
                    if (state.form.password1 === state.form.password2) {
                        state.passwordCheckFlag = true
                    } else {
                        state.passwordCheckFlag = false
                    }
                }
            } else {
                state.passwordValidFlag = false
                if (state.form.password1 === state.form.password2) {
                        state.passwordCheckFlag = true
                    } else {
                        state.passwordCheckFlag = false
                    }
            }
        }

        const passwordCheckValid = function() {
            if (state.form.password1 === state.form.password2) {
                state.passwordCheckFlag = true
            } else {
                state.passwordCheckFlag = false
            }
        }

        const emailValid = async function() {
            if (/^[0-9a-zA-Z]([-_\.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_\.]?[0-9a-zA-Z])*\.[a-zA-Z]{2,3}$/.test(state.form.email)) {
                state.emailValidFlag = true
                try{
                    let email_data = await http({
                        method: 'get',
                        url: `/users/email?email=${state.form.email}`
                    })
                    if (email_data.data==="OK") {
                        state.emailCommonFlag = false
                    } else {
                        state.emailCommonFlag = true
                    }
                    } catch(err) {
                    console.log(err.status)
                }
            } else {
                state.emailValidFlag = false
                state.emailCommonFlag = null
            }
        }
        
        return {
            state,
            store,
            signupForm,
            idValid,
            passwordValid,
            passwordCheckValid,
            emailValid,
        }
        }}
</script>

<style scoped>
input[type="password"]{
    font-family: "Gulim"
}
.form-select {
    width: 100%;
    height: 50px;
    margin-top: 3px;
    border: solid 1px #000;
}
.input-Box:nth-child(7) {
display: flex;
/* justify-content: space-evenly; */
justify-content: end;
}
button {
  background: #ffd700;
  color: #000000;
  cursor: pointer;
  width: 100px;
  margin-bottom: 20px;
  font-weight: 600;
  text-align: center;
  border-radius: 40px 80px;
}
</style>